$brand-name: 'appdb';

// light theme

$bg: #fff;
$bg-transparent: rgba(255, 255, 255, .85);
$secondary-bg: #f9f9f9;

$tertiary-bg: #f2f2f2;
$tertiary-accent: #eeeeee;

$primary: #016cfa; // links, buttons color
$dark-primary: #006cd9; // focused import border color
$light-primary: #e7f0ff; // background
$lighter-primary: #f2faff; // background

$text-primary: #3d464d;
$text-secondary: #708090;
$text-tertiary: #181f25;
$text-tertiary-accent: #525d66;

$bg-warning: #fff5cd;
$bg-success: #d7f4da;
$bg-error: #fddee6;

$shadow-color: rgba(0, 0, 0, .15);
$shadow-color-1: rgba(0, 0, 0, .08);

$img-filter: unset;

$yellow-gradient: linear-gradient(0deg, #fff6ec 0%, #fffcf3 93%);
$yellow-border: #ffefaf;
$light-yellow-gradient: linear-gradient(135deg, rgba(253, 235, 113, 0.35) 0%, rgba(248, 216, 0, 0) 100%);
$purple-gradient: linear-gradient(58deg, #ffecec 0%, #f5f2ff 100%);
$purple-border: #faf0ff;
$pink-gradient: linear-gradient(58deg, #ffecec 0%, #f5f2ff 100%);
$pink-border: #faf0ff;

$stub-bg: #dedddd;

$body-top-border-gradient: linear-gradient(270deg, #02c0fd 0%, #016cfa 100%);

$navigation-bg: #f2f2f2;

$header-device-button-gradient: linear-gradient(270deg, #1553ed 0%, #092cd7 100%);

$footer-top-bg: #0e294e;
$footer-bottom-bg: #030c19;
$footer-text-color: #b4c5d2;
$footer-link-color: #01bcfa;
$footer-button-border-color: #294a79;

$loader-secondary: #016cfa61;

$app-link-color: $primary;

$my-heading-background: #f6f7ff;
$my-buy-options-gradient: linear-gradient(225deg, #404cff 0%, #1c3372 100%);
$my-device-shadow: 0 2px 8px 0 $shadow-color;

$info-sidebar-gradient: linear-gradient(116deg, rgba(173, 245, 255, 0.35) 5%, rgba(248, 216, 0, 0) 100%);

$news-article-gradient: linear-gradient(179deg, #fbfbfb 7%, #ffffff 100%);
$news-sidebar-gradient: linear-gradient(179deg, #fff6ec 7%, #ffffff 100%);
$news-active-color: #ffdcb9;

$configure-error-text: #340b15;

$request-button-bg: #ffebeb;
$request-plus-bg: #e3e3e3;

$app-fadeout-gradient: linear-gradient(0deg, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0));

$modal-overlay-opacity: 0.2;

// dark theme

$dark-bg: #000;
$dark-bg-transparent: rgba(0, 0, 0, .4);
$dark-secondary-bg: #313342;

$dark-tertiary-bg: #4a4d61;
$dark-tertiary-accent: #6c718c;

$dark-light-primary: darken($light-primary, 87);
$dark-lighter-primary: darken($lighter-primary, 85);

$dark-text-primary: #e2e2e2;
$dark-text-secondary: lighten($text-secondary, 30);
$dark-text-tertiary: $dark-text-primary;
$dark-text-tertiary-accent: #fff;

$dark-bg-warning: darken($bg-warning, 30);
$dark-bg-success: darken($bg-success, 30);
$dark-bg-error: #520101;

$dark-shadow-color: rgba(255, 255, 255, .15);
$dark-shadow-color-1: rgba(255, 255, 255, .15);

$dark-img-filter: invert(1) hue-rotate(180deg);

$dark-yellow-gradient: linear-gradient(0deg, darken(#fff6ec, 90) 0%, darken(#fffcf3, 90) 93%);
$dark-yellow-border: darken($yellow-border, 70);
$dark-light-yellow-gradient: linear-gradient(135deg, darken(rgba(253, 235, 113, 0.35), 55) 0%, rgba(248, 216, 0, 0) 100%);
$dark-purple-gradient: linear-gradient(58deg, darken(#ffecec, 90) 0%, darken(#f5f2ff, 90) 100%);
$dark-purple-border: darken($purple-border, 75);
$dark-pink-gradient: linear-gradient(58deg, darken(#ffecec, 90) 0%, darken(#f5f2ff, 90) 100%);
$dark-pink-border: darken($pink-border, 80);

$dark-stub-bg: darken($stub-bg, 50);

$dark-body-top-border-gradient: linear-gradient(270deg, darken(#02c0fd, 20) 0%, darken(#016cfa, 10) 100%);

$dark-navigation-bg: $dark-secondary-bg;

$dark-footer-top-bg: $footer-top-bg;
$dark-footer-bottom-bg: $footer-bottom-bg;

$dark-info-sidebar-gradient: linear-gradient(116deg, rgba(73, 98, 101, 0.35) 5%, rgba(248, 216, 0, 0) 100%);

$dark-news-article-gradient: linear-gradient(179deg, rgb(25, 25, 25) 7%, #000 100%);
$dark-news-sidebar-gradient: linear-gradient(179deg, #201e1b 7%, #000 100%);
$dark-news-active-color: #392e23;

$dark-my-heading-background: $dark-tertiary-bg;
$dark-my-device-shadow: 0 0 4px 4px $dark-shadow-color;

$dark-configure-error-text: #fff;

$dark-request-button-bg: darken($request-button-bg, 85);
$dark-request-plus-bg: darken($request-plus-bg, 75);

$dark-app-fadeout-gradient: linear-gradient(0deg, rgba($dark-bg, 1), rgba($dark-bg, 0.8), rgba($dark-bg, 0));

$dark-modal-overlay-opacity: 0.6;
