@import "./src/assets/themes/appdb/variables.scss";

.link {
  color: var(--text-primary);
  display: flex;
  width: fit-content;
  align-items: center;
  font-size: 16px;

  svg {
    transform: rotate(180deg) scale(0.7);
    transform-origin: center;
    margin-right: 7px;
    margin-top: 1px;
  }
}
