@import "./src/assets/themes/appdb/variables.scss";

@import "../assets/variables";

.download {
    display: block;
    position: relative;

    &__text {
        display: block;
        width: 75px;
        overflow: hidden;
        text-overflow: ellipsis;
        position: absolute;
        left: 35px;
        top: 7px;
        font-size: 9px;
        line-height: 9px;
        color: var(--text-primary);
    }

    &__img {
        display: block;
    }
}
