@import "./src/assets/themes/appdb/variables.scss";

.version {
  &_m {
    margin: 20px 0 10px;
  }

  &__title {
    color: var(--text-tertiary);
    font-weight: 600;
    margin-bottom: 10px;
  }

  &__item {
    display: flex;

    &_center {
      align-items: center;
    }

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__install {
    display: flex;
    align-items: center;
    color: var(--text-primary);
    padding: 5px 8px;
    border: 1px solid lighten($primary, 20%);
    border-radius: 2px;
    position: relative;

    &-icon {
      margin-right: 5px;
      filter: var(--img-filter);
    }

    &-wrapper {
      margin-right: 8px;
    }

    &_pending {
      pointer-events: none;

      .version__loader {
        display: flex;
      }

      .version__tooltip {
        display: block;
      }
    }
  }

  &__loader {
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: var(--bg);
    align-items: center;
    justify-content: center;

    &-dot {
      display: inline-block;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      margin-right: 3px;
      background: var(--text-primary);
      animation: wave 1.3s linear infinite;

      &:nth-child(2) {
        animation-delay: -1.1s;
      }

      &:nth-child(3) {
        animation-delay: -0.9s;
      }
    }

    @keyframes wave {
      0%,
      60%,
      100% {
        transform: initial;
      }

      30% {
        transform: translateY(-4px);
      }
    }
  }

  &__tooltip {
    display: none;
    position: absolute;
    left: -1px;
    top: -25px;
    color: #ffffff;
    font-size: 12px;
    background: #4e4e4e;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.12);
    border-radius: 2px;
    white-space: nowrap;
    font-weight: 400;
    padding: 3px 5px;

    &:before {
      content: "";
      display: block;
      position: absolute;
      border-style: solid;
      border-width: 5px 4.5px 0 4.5px;
      border-color: #4e4e4e transparent transparent transparent;
      bottom: -5px;
      left: 15px;
    }
  }

  &__link {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    margin-right: 6px;
    word-break: break-word;
    color: $app-link-color;

    &::v-deep a {
      color: $app-link-color;
    }

    &_verified {
      filter: brightness(0) saturate(100%) invert(28%) sepia(62%)
        saturate(5528%) hue-rotate(115deg) brightness(96%) contrast(82%);
    }

    &_disabled {
      filter: brightness(0);
      pointer-events: none;
    }
  }

  &__open {
    margin-left: 3px;
    margin-top: 2px;
  }

  &__report {
    text-decoration: underline;
    color: $text-secondary;
    font-size: 12px;
    white-space: nowrap;
  }

  &__warning {
    display: flex;
    padding: 4px;
    border-radius: 2.8px;
    background-color: var(--bg-error);
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 5px;
    word-break: break-word;

    &-icon {
      padding-top: 1px;
      margin-right: 4px;

      img {
        width: 11px;
      }
    }
  }

  &__text {
    display: block;
    font-size: 12px;
    margin: 5px 0;
    line-height: 15px;
  }

  &__badge {
    display: inline-block;
    font-size: 10px;
    line-height: 12px;
    padding: 4px;
    border-radius: 2px;

    &_success {
      color: #053305;
      background-color: var(--bg-success);
    }

    &_warning {
      color: #2b2b05;
      background-color: var(--bg-warning);
    }

    &_error {
      color: var(--configure-error-text);
      background-color: var(--bg-error);
    }
  }
}
