@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.sidebar {
  overflow-y: scroll;

  @include notebook {
    padding-top: 10px;
  }
}

.block {
  padding: 15px 20px;
  border-radius: 10px;
  margin-bottom: 10px;

  @include notebook {
    padding: 10px;
  }

  &_yellow {
    background-image: var(--light-yellow-gradient);
  }

  &_blue {
    background: var(--lighter-primary);
    border-radius: 10px;
    color: var(--text-primary);
  }

  &_pink {
    background-image: var(--pink-gradient);
  }

  &_border {
    display: flex;

    &:before {
      content: "";
      display: block;
      flex: 0 0 6px;
      margin-right: 10px;
      background-color: #98accd;
      border-radius: 6px;
    }

    .block__text {
      margin: 0;
    }
  }

  &__h3 {
    font-size: 20px;
    color: var(--text-tertiary);
    font-weight: 600;
  }

  &__text {
    margin: 10px 0;
    line-height: 20px;
  }

  &__links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    max-width: 350px;

    @media (max-width: 350px) {
      flex-direction: column;

      &-or {
        margin: 4px 0;
      }
    }
  }

  &__link {
    display: inline-block;
    padding: 5px 10px;
    border: 1px solid lighten($primary, 20%);
    border-radius: 3px;
    position: relative;

    &-desc {
      position: absolute;
      right: 0;
      pointer-events: none;
      color: var(--text-primary);
      font-size: 12px;
      font-weight: 400;
      bottom: -17px;
    }
  }

  &__app-link {
    display: block;
  }

  &__button {
    color: #fff;
    font-size: 14px;
    background: $primary;
    border-radius: 3px;
    padding: 5px 10px;
    font-weight: 500;
    display: flex;
    align-items: center;
    margin: 8px 0;

    &-icon {
      margin-right: 5px;
    }
  }

  &__caption {
    color: var(--text-primary);
    font-size: 12px;
    display: inline-block;
    margin-left: 8px;
  }
}

.history {
  background-image: linear-gradient(180deg, var(--secondary-bg) 0%, var(--bg) 100%);
  border-radius: 10px;
  padding: 20px;
  max-height: 728px;

  @include notebook {
    padding: 10px;
    max-height: 400px;
  }

  &__heading {
    color: var(--text-tertiary);
    font-size: 20px;
    font-weight: 600;
    display: flex;
    align-items: center;
  }

  &__back {
    margin-right: 6px;
    margin-top: 2px;
    cursor: pointer;
  }

  &__text {
    line-height: 20px;
    margin: 10px 0 20px;
  }
}
